import React from 'react'
import styles from './ContactData.module.css'
import { actions } from '../store/index'
import { useDispatch } from 'react-redux'
const ContactDataItemPhone = (props) => {
    const dispatch = useDispatch()
    const deleteHandler = () => {
        console.log(props.contactItem.key)
        dispatch(actions.deleteContact(props.contactItem.key))
    }

    return (

        <table className={styles.tablePhone}>
            <tbody>
                <tr>
                    <th className={styles.thPhone}>שם</th>
                    <th className={styles.th}>{props.contactItem.name}</th>
                </tr>
                <tr>
                    <th className={styles.thPhone}>מספר טלפון</th>
                    <th className={styles.th}>{props.contactItem.phone}</th>
                </tr>
                <tr>
                    <th className={styles.thPhone}>תוכן ההודעה</th>
                    <th className={styles.th}>{props.contactItem.message}</th>
                </tr>
                <tr>
                    <th className={styles.thPhone}>פעולות</th>
                    <th className={styles.th}><button onClick={deleteHandler}>מחק</button></th>
                </tr>
            </tbody>
        </table>
    )
}

export default ContactDataItemPhone
