import React from 'react'
import styles from './ContactData.module.css'
import { actions } from '../store/index'
import { useDispatch } from 'react-redux'
const ContactDataItem = (props) => {
    const dispatch = useDispatch()
    const deleteHandler = () => {
        console.log(props.contactItem.key)
        dispatch(actions.deleteContact(props.contactItem.key))
    }
    return (
        <tr>
            <th className={styles.th}>
                {props.contactItem.name}
            </th>
            <th className={styles.th}>
                {props.contactItem.phone}
            </th>
            <th className={styles.th}>
                {props.contactItem.message}
            </th>
            <th className={styles.th}>
                <button onClick={deleteHandler}>מחק</button>
            </th>
        </tr>
    )
}

export default ContactDataItem
