import styles from './Header.module.css'
import {
    Link, NavLink
} from "react-router-dom";
import { useState } from 'react';


const Header = () => {
    console.log("load")
    const [openIsTrue, setOpenIsTrue] = useState(false);
    const [classOpenAnim, setClassOpenAnim] = useState(styles.header1)
    const openCloseMenu = () => {
        setOpenIsTrue((prev) => !prev)
        if (!openIsTrue) {
            setClassOpenAnim((prev) => `${styles.header1} ${styles.anim}`)
        } else {
            setClassOpenAnim((prev) => styles.header1)
        }
    }
    const cancelAnim = () => {
        setClassOpenAnim((prev) => styles.header1)
    }
    return (
        <div>
            {/* this Header is shown just in phones: */}
            <div className={styles.header1}>
                <div className={styles.backbtn}><button onClick={openCloseMenu} className={styles.menubtn}><strong>|||</strong></button></div>
                <div className={classOpenAnim}>
                    <NavLink activeClassName={styles.active} onClick={cancelAnim} className={styles.a} exact to="/Home">{openIsTrue && <div className={styles.headerdivs}><strong>ראשי</strong></div>}</NavLink>
                    <NavLink activeClassName={styles.active} onClick={cancelAnim} className={styles.a} to="/Gallery">{openIsTrue && <div className={styles.headerdivs}><strong>גלריה</strong></div>}</NavLink>
                    <NavLink activeClassName={styles.active} onClick={cancelAnim} className={styles.a} to='/Video'>{openIsTrue && <div className={styles.headerdivs}><strong>המלצות צפייה</strong></div>}</NavLink>
                    <NavLink activeClassName={styles.active} onClick={cancelAnim} className={styles.a} to='/Links'>{openIsTrue && <div className={styles.headerdivs}><strong>קישורים</strong></div>}</NavLink>
                    <NavLink activeClassName={styles.active} onClick={cancelAnim} className={styles.a} to='/Contact'>{openIsTrue && <div className={styles.headerdivs}><strong>צור קשר</strong></div>}</NavLink>
                    <NavLink activeClassName={styles.active} onClick={cancelAnim} className={styles.amanager} to='/ContactData'>{openIsTrue && <div className={styles.headerdivs}><strong>הודעות</strong></div>}</NavLink>
                </div>
            </div>
            {/* this Header is shown just in computers: */}
            <div className={styles.header2}>
                <NavLink activeClassName={styles.active} className={styles.a} exact to="/Home"><div className={styles.headerdivs}><strong>ראשי</strong></div></NavLink>
                <NavLink activeClassName={styles.active} className={styles.a} to="/Gallery"><div className={styles.headerdivs}><strong>גלריה</strong></div></NavLink>
                <NavLink activeClassName={styles.active} className={styles.a} to='/Video'><div className={styles.headerdivs}><strong>המלצות צפייה</strong></div></NavLink>
                <NavLink activeClassName={styles.active} className={styles.a} to='/Links'><div className={styles.headerdivs}><strong>קישורים</strong></div></NavLink>
                <NavLink activeClassName={styles.active} className={styles.a} to='/Contact'><div className={styles.headerdivs}><strong>צור קשר</strong></div></NavLink>
                <NavLink activeClassName={styles.active} className={styles.amanager} to='/ContactData'><div className={styles.headerdivs}><strong>הודעות</strong></div></NavLink>
            </div>
        </div>
    )

}

export default Header