import { useSelector } from 'react-redux'
import styles from './ContactData.module.css'
import ContactDataItem from './ContactDataItem'
import ContactDataItemPhone from './ContactDataItemPhone'
import { useState } from 'react'
const ContactData = () => {
    const contacts = useSelector(state => state.contact.contacts)
    const contactsJSX = contacts.map((c) =>
        <ContactDataItem contactItem={c} />
    )
    const contactsPhoneJSX = contacts.map((c) =>
        <ContactDataItemPhone contactItem={c} />
    )
    const thereIsMessages = (contacts == "")
    console.log(contacts)
    console.log(thereIsMessages)
    return (
        <div className={styles.flex}>
            {thereIsMessages && <h5>אין הודעות להציג</h5>}
            {contactsPhoneJSX}
            {!thereIsMessages && <table className={styles.tableComp}>
                <thead className={styles.thead}>
                    <tr>
                        <th className={styles.th}>שם</th>
                        <th className={styles.th}>מספר טלפון</th>
                        <th className={styles.th}>תוכן ההודעה</th>
                        <th className={styles.th}>פעולות</th>
                    </tr>
                </thead>
                <tbody>
                    {contactsJSX}
                </tbody>
            </table>}

        </div>
    )
}

export default ContactData
