import './App.css';
import GalleryChanges from './pages/GalleryChanges';
import Video from './pages/Video';
import Home from './pages/Home';
import React from "react";
import {
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import Header from './pages/Header';
import Links from './pages/Links';
import Contact from './pages/Contact'
import ContactData from './pages/ContactData';
import Gallery from './pages/Gallery';
function App() {
  return (
    <div>
      <Header />

      <Switch>
        <Route path="/Gallery">
          <Gallery />
        </Route>
        <Route path="/Video">
          <Video />
        </Route>
        <Route path="/Links">
          <Links />
        </Route>
        <Route path="/ContactData">
          <ContactData />
        </Route>
        <Route path="/Contact">
          <Contact />
        </Route>
        <Route path="/Home">
          <Home />
        </Route>

        <Route path="/">
          <Redirect to='/Home' />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
