import styles from './Gallery.module.css'
import left from '../images/left.png'
import right from '../images/right.png'
import ship from '../images/ship.jpeg'
import chess from '../images/chess.jpeg'
import { useState } from 'react'
import car from '../images/car.jpeg'
import deer from '../images/deer.jpeg'
import drums from '../images/drums.jpeg'
import eagle from '../images/eagle.jpeg'
import fox from '../images/fox.jpeg'
import guitar from '../images/guitar.jpeg'
import guitar2 from '../images/guitar2.jpeg'
import horse from '../images/horse.jpeg'
import horse2 from '../images/horse2.jpeg'
import horse3 from '../images/horse3.jpeg'
import leaf from '../images/leaf.jpeg'
import legoman from '../images/legoman.jpg'
import lion from '../images/lion.jpeg'
import lion2 from '../images/lion2.jpeg'
import netzach from '../images/netzach.jpeg'
import nof from '../images/nof.jpeg'
import rozenberg from '../images/rozenberg.jpeg'
import ship2 from '../images/ship2.jpeg'
import tree from '../images/tree.jpeg'
import violin from '../images/violin.jpeg'
import cinor from '../images/cinor.jpeg'
import trees from '../images/trees.jpeg'
import birds from '../images/birds.jpeg'
import brosh from '../images/brosh.jpeg'
import dekel from '../images/dekel.jpeg'
import fire from '../images/fire.jpeg'
import firstguitar from '../images/firstguitar.jpeg'
import hamazon from '../images/hamazon.jpeg'
import horseguitar from '../images/horseguitar.jpeg'
import lionguitar from '../images/lionguitar.jpeg'
import magen from '../images/magen.jpeg'
import me from '../images/me.jpeg'
import pakal from '../images/pakal.jpeg'
import rengler from '../images/rengler.jpeg'
import tanami from '../images/tanami.jpeg'
import treeguitar from '../images/treeguitar.jpeg'
import tukim from '../images/tukim.jpeg'
const Gallery = () => {
    const [intarsiaSrc, setIntarsiaSrc] = useState(ship)
    const [fretworkSrc, setFretworkSrc] = useState(chess)

    const intarsiaSrcs = [
        ship,
        deer,
        eagle,
        fox,
        guitar,
        guitar2,
        horse,
        horse2,
        horse3,
        leaf,
        lion,
        lion2,
        netzach,
        nof,
        rozenberg,
        ship2,
        tree,
        trees,
        violin,
        legoman,
        drums,
        car
    ]

    const fretworkSrcs = [
        chess,
        birds,
        brosh,
        cinor,
        dekel,
        fire,
        firstguitar,
        hamazon,
        horseguitar,
        lionguitar,
        magen,
        me,
        pakal,
        rengler,
        tanami,
        treeguitar,
        tukim
    ]
    const changeIntarsiaRight = () => {
        if (intarsiaSrcs.indexOf(intarsiaSrc) < (intarsiaSrcs.length - 1)) {
            setIntarsiaSrc((prev) => intarsiaSrcs[intarsiaSrcs.indexOf(prev) + 1])
        } else {
            setIntarsiaSrc((prev) => intarsiaSrcs[0])
        }
    }
    const changeIntarsiaLeft = () => {
        if (intarsiaSrcs.indexOf(intarsiaSrc) > 0) {
            setIntarsiaSrc((prev) => intarsiaSrcs[intarsiaSrcs.indexOf(prev) - 1])
        } else {
            setIntarsiaSrc((prev) => intarsiaSrcs[intarsiaSrcs.length - 1])
        }
    }
    const changeFretworkRight = () => {
        if (fretworkSrcs.indexOf(fretworkSrc) < (fretworkSrcs.length - 1)) {
            setFretworkSrc((prev) => fretworkSrcs[fretworkSrcs.indexOf(prev) + 1])
        } else {
            setFretworkSrc((prev) => fretworkSrcs[0])
        }
    }
    const changeFretworkLeft = () => {
        if (fretworkSrcs.indexOf(fretworkSrc) > 0) {
            setFretworkSrc((prev) => fretworkSrcs[fretworkSrcs.indexOf(prev) - 1])
        } else {
            setFretworkSrc((prev) => fretworkSrcs[fretworkSrcs.length - 1])
        }
    } 
    return (
        <div>
            <h1>היצירות שלי</h1>
            <div className={styles.flex}>
                <div>
                    <h2>אינטרסיה - intarsia</h2>
                    <div className={styles.photos}>
                        <img className={styles.arrow} src={right} onClick={changeIntarsiaRight} />
                        <a href={intarsiaSrc} target="_blank" className="imghref"><img src={intarsiaSrc} className={styles.img} alt="wood intarsia - תמונות אינטרסיה" /></a>
                        <img className={styles.arrow} src={left} onClick={changeIntarsiaLeft} />
                    </div>
                </div>

                <div>
                    <h2>פרטוורק - fretwork</h2>
                    <div className={styles.photos}>
                        <img className={styles.arrow} src={right} onClick={changeFretworkRight} />
                        <a href={fretworkSrc} target="_blank" className="imghref2"><img src={fretworkSrc} className={styles.img} alt="fretwork - תמונות פרטוורק" /></a>
                        <img className={styles.arrow} src={left} onClick={changeFretworkLeft} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Gallery