import { createSlice, configureStore } from '@reduxjs/toolkit'

//Counter slice:
const contactsSlice = createSlice({
    initialState: {
        contacts: []
    },
    name: 'contacts',
    reducers: {
        // toggleFavorite: (state, action) => {
        //     let index = state.contacts.findIndex((c) => c.id === action.payload)
        //     console.log(action)
            
            
        // },
        addContact: (state, action) => {
            state.contacts.push(action.payload)
        },
        deleteContact: (state, action) => {
            state.contacts = state.contacts.filter((c) => c.key != action.payload)
        }
    }
})

const store = configureStore({
    reducer: {
        contact: contactsSlice.reducer
    }
})


//actions are: increment, decrement , flip
export const actions = contactsSlice.actions
export default store
