import React from 'react'
import styles from './Contact.module.css'
import { useDispatch } from 'react-redux'
import { actions } from '../store/index'
import { useState } from 'react'
const Contact = () => {
    let newContact = {
        key: Math.random(),
        name: '',
        phone: '',
        message: ''
    }
    const [contactDone, setContactDone] = useState(false)
    const addName = (ev) => {
        newContact.name = ev.target.value
        setContactDone((prev) => false)
    }
    const addPhone = (ev) => {
        newContact.phone = ev.target.value
        setContactDone((prev) => false)
    }
    const addMessage = (ev) => {
        newContact.message = ev.target.value
        setContactDone((prev) => false)
    }
    const dispatch = useDispatch()

    const [fillAllInputs, setFillAllInputs] = useState(false)

    const addContactHandler = (ev) => {
        if (newContact.name != '' && newContact.phone != '' && newContact.message != '') {
            dispatch(actions.addContact(newContact))
            newContact = {
                key: Math.random(),
                name: '',
                phone: '',
                message: ''
            }
            setContactDone((prev) => true)
            setFillAllInputs((prev) => false)
        } else {
            setFillAllInputs((prev) => true)
        }
    }

    return (
        <div>
            <div className={styles.flex}>
                <form className={styles.form}>
                    <h3>שימו לב, האתר בשיפוצים<br/> והצור קשר לא מגיע אלי כרגע...</h3>
                    <label>שם מלא</label><br />
                    <input type="text" onChange={addName} /><br />

                    <label>מס' טלפון</label><br />
                    <input type="text" onChange={addPhone} /><br />

                    <label>תוכן ההודעה</label><br />
                    <textarea onChange={addMessage} rows="4"></textarea><br />
                    {fillAllInputs && <div style={{ color: "red" }}>יש למלא את כל השדות</div>}
                    {contactDone && <div style={{ color: "green" }}>ההודעה נשלחה בהצלחה</div>}
                    <button type="reset" onClick={addContactHandler}>שליחה</button>
                    <h3>הפרטים שלי:</h3>
                    <ul className={styles.ul}>
                        <li>איתמר שטרן</li>
                        <li>משעול הרקפת 1, גינות שומרון</li>
                        <li><ins><a href="tel:+972532808626">053-2808626</a></ins></li>
                        <li><ins><a href="mailto:ita767@gmail.com">ita767@gmail.com</a></ins></li>
                    </ul>
                </form>

                <iframe
                    className={styles.iframe} src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3377.2487048153125!2d35.087185685588956!3d32.170567521967335!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x151d247e6599cbbf%3A0x16853bfea2a1a24f!2z157Xqdei15XXnCDXlNeo16fXpNeqIDEsINen16jXoNeZINep15XXnteo15XXnw!5e0!3m2!1siw!2sil!4v1620048342784!5m2!1siw!2sil"
                    allowFullScreen="" loading="lazy"></iframe>
            </div>

        </div>
    )
}

export default Contact
